@import './loading';

[data-component='bookmarks'] {
    height: 100%;
}

.quicklinks {
    &__list {
        @include pnp-scroll-x;
        display: flex;
    }

    &__link {
        width: 75px;
        height: 75px;

        &:not(:last-child) {
            margin-right: 15px;
        }
    }

    &__link-icon {
        display: flex;
        justify-content: center;
        padding-bottom: 10px;
    }

    &__button {
        @include pnp-reset-button;
        @include pnp-svg-icon-classes;
        @include pnp-standard-box-shadow;

        width: 100%;
        height: 100%;
        padding: 10px;
        background-color: $white;
        border: solid 1px $grey-20;

        &--dashed {
            color: $grey-70;
            text-align: center;
            border: 1px dashed $grey-20;
        }

        &:hover,
        &:focus {
            background-color: $grey-10;
        }
    }

    &__link-title {
        @include pnp-typeface(text-14-bold, false);
        overflow: hidden;
        color: $grey-70;
        white-space: nowrap;
        text-align: center;
        text-overflow: ellipsis;
    }
}

.quicklinks-edit {
    @include pnp-svg-icon-classes;
    @include pnp-reset-button;
    line-height: 1;
}

.quicklinks-modal {
    @include pnp-svg-icon-classes;
    @include pnp-media-breakpoint-down(sm) {
        .quicklinks-modal__content {
            width: 100%;
            height: 100%;
        }
    }

    &__content {
        display: flex;
        flex-direction: column;
        min-width: 400px;
    }

    &__header {
        position: relative;
        display: flex;
        flex-direction: column;
        padding: 24px;
        padding-bottom: 16px;
        border-bottom: solid 1px $grey-20;
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-18-bold, false);
    }

    &__subtitle {
        @include pnp-reset-heading;
        @include pnp-typeface(text-14, false);
        margin-top: 12px;
    }

    &__header-close {
        @include pnp-reset-button;
        position: absolute;
        top: 24px;
        right: 24px;
    }

    &__body {
        padding: 24px;
    }

    &__container {
        display: flex;
        margin-bottom: 14px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__edit-button {
        @include pnp-reset-button;
        @include pnp-standard-box-shadow;
        display: flex;
        align-items: center;
        width: 100%;
        padding: 10px;
        background-color: $grey-10;
        border: 1px solid $grey-20;
        border-radius: $rounded-corner-radius;

        svg {
            display: none;
            width: 20px;
            margin-left: auto;
        }

        &:hover,
        &:focus {
            svg {
                display: block;
            }
        }
    }

    &__edit-button-title {
        @include pnp-typeface(text-14-bold, false);
        max-width: 230px;
        padding-left: 6px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__delete-button {
        @include pnp-reset-button;
        margin: 10px;
        margin-left: 20px;
        color: $grey-70;

        &:hover,
        &:focus {
            color: $black;
        }
    }

    &__footer-buttons-container {
        width: 100%;
    }

    &__footer {
        display: flex;
        justify-content: flex-start;
        margin-top: auto;
        padding: 24px;
        padding-top: 0;
    }

    &__footer-right-aligned .quicklinks-modal__footer-buttons-container {
        display: flex;
        justify-content: flex-end;
        width: 100%;
    }

    &__footer-button {
        @include pnp-button;
        min-width: 90px;
        color: $black;
        background-color: $grey-10;
        border: solid 1px transparent;

        &:first-child {
            margin-right: 8px;
        }

        &--save {
            color: $white;
            background: $primary-a;
        }

        &--dashed {
            width: 100%;
            background-color: transparent;
            border: 1px dashed $grey-20;
        }
    }

    .custom-form {
        padding: 0;

        input,
        .sq-form-question,
        .sq-form-question-title {
            margin-bottom: 5px;
            color: $grey-80;
        }

        .sq-form-question-text {
            margin: 0;
        }

        .sq-form-error {
            display: block;
            height: 22px;
            color: $utility-alert;
        }
    }
}
