.primary-nav-vertical-mobile-quicklinks {
    @include pnp-standard-box-shadow;
    @include pnp-typeface(text-12, false);

    position: fixed;
    bottom: 0;
    left: 0;
    z-index: $fixed-side-nav;
    display: flex;
    align-items: center;
    width: 100%;
    height: $primary-nav-bottom-height;
    background-color: $white;

    @include pnp-media-breakpoint-up(
        $primary-nav-vertical-changeover-breakpoint
    ) {
        display: none;
    }

    &__list {
        @include pnp-list-reset;
        display: flex;
        justify-content: space-evenly;
        width: 100%;
    }
}

.primary-nav-vertical-mobile-quicklink {
    &--current {
        .primary-nav-vertical-mobile-quicklink__link {
            @include pnp-typeface(text-12-bold, false);
            color: $black;
            background: var(--primary-nav-vertical-highlight);
        }
    }

    &__link {
        display: flex;
        flex-direction: column;

        align-items: center;
        padding: 5px 6px;
        color: $grey-70;
        text-decoration: none;
        border-radius: $rounded-corner-radius;

        &:hover,
        &:focus {
            color: $black;
            background: var(--primary-nav-vertical-highlight);
        }
    }

    .svg-icon {
        width: 16px;
        height: 16px;
    }
}
