@import './loading';

.emails-listing {
    &__heading {
        @include pnp-reset-heading;
        @include pnp-typeface(text-14-bold, false);
        margin-bottom: 10px;
        color: $grey-80;
        text-transform: uppercase;
    }

    &__footer-button {
        @include pnp-svg-icon-classes;
        @include pnp-link-button;
        @include pnp-typeface(text-14, false);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 5px;
        text-decoration: none;

        .svg-icon {
            @include pnp-typeface(text-12, false);
            margin-right: 6px;
        }
    }

    &__error-retry {
        @include pnp-link-button;
    }
}

.emails-list {
    @include pnp-list-reset;

    &__item {
        margin-bottom: 10px;
    }
}

.email-heading {
    display: flex;

    &__details {
        display: flex;
        flex-grow: 1;
        flex-wrap: nowrap;
        align-items: center;
        min-width: 0;
    }

    &__title {
        @include pnp-typeface(text-14, false);
        overflow: hidden;
        color: $grey-80;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__from {
        @include pnp-typeface(text-12-bold, false);
        flex-shrink: 0;
        margin-right: 10px;
        padding: 4px;
        color: $grey-70;
        text-transform: uppercase;
        background-color: $grey-10;
    }

    &__link {
        @include pnp-svg-icon-classes;
        @include pnp-typeface(text-14, false);
        @include pnp-link-button;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        color: $grey-70;
    }

    &--not-read {
        &::before {
            margin-right: 5px;
            color: $utility-focus;
            content: '\25CF'; //●
        }

        .email-heading__from {
            color: $primary-a;
            background-color: rgba(93, 154, 248, 0.15);
        }
    }
}

.email-subtitle {
    @include pnp-typeface(text-12, false);
    flex-direction: row;
    margin-top: 10px;
    color: $grey-70;

    @include pnp-media-breakpoint-down(md) {
        @include pnp-visuallyhidden;
    }
}

.email-collapsible {
    @include pnp-typeface(text-12, false);
    margin: 10px 0;
    color: $grey-70;

    @include pnp-media-breakpoint-up(md) {
        @include pnp-visuallyhidden;
    }

    &__subtitle {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__content {
        padding: 10px 0;
        word-break: break-word;
    }

    &__show-more {
        display: flex;
        flex-direction: row-reverse;
        align-items: flex-start;
        justify-content: space-between;
        width: 100%;
    }
}

.emails-listing-sign-in {
    &__message {
        a {
            @include pnp-typeface(text-16, no-margin);
            @include pnp-link-text;
        }

        .google-api-auth,
        .microsoft-auth {
            margin-top: 5px;
        }
    }
}
