.primary-nav-vertical--collapsed {
    .primary-nav-vertical-top {
        position: fixed;
        left: $primary-nav-vertial-collapsed-width;
        width: calc(100% - $primary-nav-vertial-collapsed-width);

        @include pnp-media-breakpoint-down(md) {
            left: 0;
            width: 100%;
        }
    }
}

.primary-nav-vertical-top {
    position: fixed;
    top: 0;
    left: $primary-nav-vertial-width;
    z-index: $fixed-header;
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: calc(100% - $primary-nav-vertial-width);
    height: $primary-nav-top-height;
    padding: 13px 30px;
    border-bottom: 1px solid $grey-20;

    @include pnp-media-breakpoint-down(
        $primary-nav-vertical-changeover-breakpoint
    ) {
        left: 0;
        width: 100%;
        padding-right: 15px;
        padding-left: 15px;
        background: $gradient-a;
    }

    &__left {
        display: flex;
        align-items: center;
    }

    &__logo {
        margin-right: 10px;

        @include pnp-media-breakpoint-up(
            $primary-nav-vertical-changeover-breakpoint
        ) {
            display: none;
        }
    }

    &__title {
        @include pnp-reset-heading;
        @include pnp-typeface(heading-20-bold, false);
        overflow: hidden;
        color: $grey-80;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__button {
        @include pnp-button;
        display: inline-flex;
        align-items: center;
        margin-right: 12px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__button-label {
        margin-left: 8px;
    }
}
