.calendar__banner + .calendar__body .calendar-month-view {
    $cdv-banner-height: 38px;
    /* stylelint-disable-next-line selector-max-compound-selectors */
    thead .fc-scroller {
        padding-top: #{26px + $cdv-banner-height};
    }
}

.calendar-month-view {
    height: 100%;

    .fc {
        .fc-daygrid-event {
            &:hover,
            &:focus {
                background: $grey-10;
            }
        }
        // Colour the dot
        .fc-daygrid-event-dot {
            border-color: $primary-a;
        }
        // Past dots are coloured lighter
        .fc-day-past .fc-daygrid-event-dot {
            border-color: $primary-a-light;
        }

        .fc-daygrid-day.fc-day-today {
            background: rgba($utility-light, 0.1);
        }

        .fc-day-today .fc-daygrid-day-number {
            //text-decoration: underline
            &::after {
                position: absolute;
                bottom: 2px;
                left: 2px;
                width: calc(100% - 4px);
                height: 1.5px;
                background: $black;
                content: '';
            }
        }

        // Border corner
        .fc-scrollgrid {
            border-radius: $rounded-corner-radius;
        }

        .fc-scrollgrid-section > td {
            border-bottom-right-radius: $rounded-corner-radius;
        }

        .fc-scrollgrid-section > th {
            border-top-right-radius: $rounded-corner-radius;
        }
    }
}
