.collapsible-card {
    display: flex;
    flex-direction: column;
    height: 100%;
    max-height: 100%;
    background-color: $white;

    &--collapsed {
        .collapsible-card__header {
            border-bottom-right-radius: $rounded-corner-radius;
            border-bottom-left-radius: $rounded-corner-radius;
        }

        .collapsible-card__body {
            display: none;
        }
    }

    &__header {
        $padding: 14px;
        $height: 24px;
        @include pnp-typeface(text-14, false);
        @include pnp-reset-heading();
        @include pnp-typeface(text-14, false);
        @include pnp-svg-icon-classes();
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        min-height: #{$padding * 2 + $height};
        padding: 14px;
        color: $grey-80;
        line-height: 24px;
        background-color: $white;
        border: 1px solid $grey-20;

        border-top-left-radius: $rounded-corner-radius;
        border-top-right-radius: $rounded-corner-radius;

        &button {
            @include pnp-reset-button();
        }

        &-right {
            display: flex;
            align-items: inherit;
        }

        &-text {
            @include pnp-reset-heading();
            margin-right: 14px;
            color: $grey-80;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    &__header-text--sub-title {
        margin-right: 14px;
    }

    &__more-menu-icon {
        color: $grey-80;
        transform: rotate(-90deg);
    }

    &__collapsed {
        transform: rotate(90deg);
    }

    &__body {
        flex-grow: 1;
        border: 1px solid $grey-20;
        border-top: 0;
        border-bottom-right-radius: $rounded-corner-radius;
        border-bottom-left-radius: $rounded-corner-radius;
    }

    &__contents {
        flex-grow: 1;
        padding: 14px;
    }

    &__contents--scrollable {
        @include pnp-scroll-y;
    }
}
