.primary-nav-vertical {
    --primary-nav-vertical-highlight: rgba(255, 255, 255, 0.4);
    @include pnp-svg-icon-classes;
    @include pnp-typeface(text-16, false);

    &__side {
        position: fixed;
        top: 0;
        left: 0;
        z-index: $fixed-side-nav;
        display: flex;
        flex-direction: column;
        width: $primary-nav-vertial-width;
        height: 100vh;

        padding: 18px 0;
        padding-bottom: 110px;
        border-right: 1px solid $grey-20;

        @include pnp-media-breakpoint-down(
            $primary-nav-vertical-changeover-breakpoint
        ) {
            display: none;
        }
    }

    &__collapse {
        @include pnp-reset-button;
        position: absolute;
        right: 8px;
        bottom: 60px;
        display: flex;
        padding: 10px;
        border-radius: $rounded-corner-radius;

        &:hover,
        &:focus {
            background: var(--primary-nav-vertical-highlight);
        }
    }

    &__collapse-label {
        margin-left: 8px;
    }
}

.primary-nav-vertical--collapsed {
    .primary-nav-vertical__side {
        width: $primary-nav-vertial-collapsed-width;
    }

    // Hide the text only visually on many controls
    .primary-nav-vertical-header__site-name,
    .primary-nav-vertical-quicklink__label,
    .primary-nav-vertical__collapse-label {
        @include pnp-visuallyhidden;
    }

    .primary-nav-vertical-header {
        padding: 0 8px;

        &__site-link {
            justify-content: center;
        }

        &__image {
            margin: 0;
        }
    }

    .primary-nav-vertical-quicklinks {
        padding: 0 8px;
    }

    .primary-nav-vertical-quicklink {
        &__link {
            padding: 12px;
        }

        &__icon {
            margin: 0;
        }
    }

    .primary-nav-vertical-menu__collapsed-menu {
        display: block;
    }

    .primary-nav-vertical-menu {
        position: relative;
        flex-grow: 1;
        padding: 0 8px;
        padding-top: 15px;
        overflow: initial;

        &--active {
            > .primary-nav-vertical-menu__content {
                @include pnp-standard-box-shadow;
                position: absolute;
                top: 15px;
                left: 56px;
                display: block;
                min-width: 204px; // Same internal width as it has uncollapsed
                padding: 20px;
                background: $white;
                border-radius: $rounded-corner-radius;
            }

            .primary-nav-vertical-menu__collapsed-menu {
                background: var(--primary-nav-vertical-highlight);
                border-radius: $rounded-corner-radius;
            }
        }

        &__content {
            @include pnp-scroll-x;
            max-height: 100%;
        }
    }

    .primary-nav-vertical-menu__content {
        display: none;
    }

    .primary-nav-vertical__collapse-icon {
        transform: rotate(180deg);
    }
}

.primary-nav-vertical-header {
    margin-bottom: 40px;
    padding: 0 18px;

    &__site-link {
        display: flex;
        align-items: center;
        padding: 5px 0;
        color: $primary-a;
        text-decoration: none;
    }

    &__image {
        width: 25px;
        height: 25px;
        margin-right: 5px;
    }

    &__site-name {
        @include pnp-typeface(heading-20, false);
    }
}

.primary-nav-vertical-quicklinks {
    margin-bottom: 15px;
    padding: 0 18px;

    &__list {
        @include pnp-list-reset;
    }
}

.primary-nav-vertical-quicklink {
    &--current {
        .primary-nav-vertical-quicklink__link {
            @include pnp-typeface(text-16-bold, false);
            color: $black;
            background: var(--primary-nav-vertical-highlight);
        }
    }

    &__icon {
        margin-right: 8px;

        &.svg-icon {
            width: 16px;
            height: 16px;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        padding: 10px;
        color: inherit;
        text-decoration: none;
        border-radius: $rounded-corner-radius;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }
}

.primary-nav-vertical-menu {
    @include pnp-scroll-x;
    padding: 0 18px;
    padding-top: 30px;
    border-top: 1px solid $grey-40;

    &__collapsed-menu {
        @include pnp-reset-button;
        display: none;
        padding: 12px;
        line-height: 16px;
    }

    &__collapsed-icon {
        &.svg-icon {
            width: 16px;
            height: 16px;
        }
    }

    &__collapsed-label {
        @include pnp-visuallyhidden;
    }

    &__label {
        @include pnp-typeface(text-14-bold, false);
        margin-bottom: 10px;
        padding: 0 10px;
        color: $black;
        text-transform: uppercase;
    }

    &__list {
        @include pnp-list-reset;

        .primary-nav-vertical-menu__list {
            display: none;
            margin-left: 18px;

            &--active {
                display: block;
            }
        }
    }
}

.primary-nav-vertical-menu-item {
    &--current {
        > .primary-nav-vertical-menu-item__link,
        > .primary-nav-vertical-menu-item__expander {
            @include pnp-typeface(text-16-bold, false);
            color: $black;
            background: var(--primary-nav-vertical-highlight);
        }
    }

    &__link,
    &__expander {
        display: block;
        padding: 5px 10px;
        color: inherit;
        text-decoration: none;
        border-radius: $rounded-corner-radius;

        &:hover,
        &:focus {
            text-decoration: underline;
        }
    }

    &__expander {
        @include pnp-reset-button;
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding: 5px 10px;

        &[aria-expanded='true'] {
            .primary-nav-vertical-menu-item__expander-icon {
                transform: rotate(270deg);
            }
        }
    }

    &__expander-icon {
        transform: rotate(90deg);
    }
}

@import './navTop';
@import './mobileQuicklinks';
