.widget-grid-wrapper {
    @include pnp-svg-icon-classes;

    &--non-default-tab-active {
        @include pnp-media-breakpoint-down(md) {
            .widget-grid {
                display: block;
            }

            .widget-grid__container {
                display: none;

                &--show-mobile-tab {
                    display: block;
                    width: auto;
                    height: 100%;
                }
            }
        }
    }
}

.widget-grid {
    display: grid;
    grid-auto-rows: 0; // Disable implicit rows
    grid-auto-columns: 0; // Disable implicit columns
    grid-template-rows: repeat(6, auto);
    grid-template-columns: 100%;
    row-gap: 10px;

    @include pnp-media-breakpoint-up(md) {
        grid-template-rows: repeat(6, minmax(160px, auto));
        grid-template-columns: repeat(2, minmax(0, 1fr));
        column-gap: 10px;
    }

    @include pnp-media-breakpoint-up(xl) {
        grid-template-rows: repeat(6, minmax(160px, 1fr));
        grid-template-columns: repeat(3, minmax(0, 1fr));
    }

    &--has-tablist {
        @include pnp-media-breakpoint-down(md) {
            margin-bottom: 45px;
        }
    }

    &--desktop-columns-one {
        @include pnp-media-breakpoint-up(xl) {
            grid-template-columns: 100%;
        }
    }

    &--desktop-columns-two {
        @include pnp-media-breakpoint-up(xl) {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &--tablet-columns-one {
        @include pnp-media-breakpoint-between(md, xl) {
            grid-template-columns: 100%;
        }
    }
}

.widget-grid__container {
    @include pnp-media-breakpoint-down(md) {
        @for $i from 1 to 10 {
            &--startRowMobile-#{$i} {
                grid-row-start: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--endRowMobile-#{$i} {
                grid-row-end: #{$i};
            }
        }
    }

    @include pnp-media-breakpoint-up(md) {
        @for $i from 1 to 3 {
            &--startColumnTablet-#{$i} {
                grid-column-start: #{$i};
            }
        }

        @for $i from 1 to 3 {
            &--endColumnTablet-#{$i} {
                grid-column-end: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--startRowTablet-#{$i} {
                grid-row-start: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--endRowTablet-#{$i} {
                grid-row-end: #{$i};
            }
        }
    }

    @include pnp-media-breakpoint-up(xl) {
        @for $i from 1 to 4 {
            &--startColumnDesktop-#{$i} {
                grid-column-start: #{$i};
            }
        }

        @for $i from 1 to 4 {
            &--endColumnDesktop-#{$i} {
                grid-column-end: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--startRowDesktop-#{$i} {
                grid-row-start: #{$i};
            }
        }

        @for $i from 1 to 10 {
            &--endRowDesktop-#{$i} {
                grid-row-end: #{$i};
            }
        }
    }

    @include pnp-media-breakpoint-between(0, md) {
        // Hide the widget container only on mobile
        &--mobile-grid-hidden {
            display: none;
            width: 0;
        }
    }

    @include pnp-media-breakpoint-between(md, xl) {
        // Hide the widget grid only on tablet
        &--tablet-grid-hidden {
            display: none;
            width: 0;
        }
    }

    @include pnp-media-breakpoint-up(xl) {
        // Hide the widget grid only on desktop up
        &--desktop-grid-hidden {
            display: none;
            width: 0;
        }
    }

    // Cuff off any overflow and force content to stick to its provided grid size
    > * {
        height: 100%;
        overflow: hidden;
    }
}

$widget-grid-mobile-nav-height: 50px;

.widget-grid-mobile-nav {
    @include pnp-standard-box-shadow;
    @include pnp-typeface(text-12, false);

    position: fixed;
    bottom: 0;
    left: 0;
    z-index: $widget-grid-mobile-nav;
    display: none;
    justify-content: space-evenly;
    width: 100%;
    height: $widget-grid-mobile-nav-height;
    background-color: $white;

    @include pnp-media-breakpoint-down(md) {
        display: flex;
    }

    &__item {
        @include pnp-reset-button;
        padding: 0 6px;
        color: $grey-70;
        border-radius: $rounded-corner-radius;

        &--current {
            @include pnp-typeface(text-12-bold, true);
            color: $black;

            .widget-grid-mobile-nav__item-inner {
                background-color: $grey-10;
            }
        }

        &:hover,
        &:focus {
            color: $black;

            .widget-grid-mobile-nav__item-inner {
                background: $grey-10;
            }
        }
    }

    &__item-inner {
        @include pnp-svg-icon-classes;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 4px 6px;
        border-radius: 3px;
    }

    &__item-icon {
        width: 12px;
        height: 12px;
    }
}

.widget-item {
    display: flex;
    flex-direction: column;
    height: 100%;

    &__header {
        $padding: 14px;
        $height: 24px;

        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;

        width: 100%;
        min-height: #{$padding * 2 + $height};
        padding: $padding;
        color: $grey-80;
        line-height: $height;
        background-color: $white;

        border: 1px solid #dadada;
        border-bottom: 0;
        border-top-left-radius: $rounded-corner-radius;
        border-top-right-radius: $rounded-corner-radius;
    }

    &__title {
        @include pnp-reset-heading();
        @include pnp-typeface(text-16, false);
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    &__sub-title {
        @include pnp-typeface(text-14, false);
    }

    &__body {
        flex-grow: 1;
        padding: 15px;
        border: 1px solid $grey-20;
        border-bottom-right-radius: $rounded-corner-radius;
        border-bottom-left-radius: $rounded-corner-radius;

        &--scrollable {
            @include pnp-scroll-y;
        }
    }
}
